* {
   box-sizing: border-box;
}

body {
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
       Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   background-color: #fafafa;
   color: #212121;
   line-height: 1.5;
}

img {
   display: block;
   max-width: 100%;
   border: 1px solid #0d161b;
   border-radius: 5px;
}

.is-hidden {
   position: absolute;
   width: 1px;
   height: 1px;
   margin: -1px;
   border: 0;
   padding: 0;
   white-space: nowrap;
   clip-path: inset(100%);
   clip: rect(0 0 0 0);
   overflow: hidden;
}

.search-form {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   display: flex;
   gap: 30px;
   align-items: center;
   justify-content: center;
   padding: 20px 0 20px 0;
   background: rgba(185, 242, 250, 0.959);
   box-shadow: 2px 10px 8px 0px rgba(99, 214, 207, 0.68);
       -webkit-box-shadow: 2px 10px 8px 0px rgba(99, 214, 207, 0.68);
       -moz-box-shadow: 2px 10px 8px 0px rgba(99, 214, 207, 0.68);
}

input {
   width: 300px;
   height: 40px;
   padding: 5px 5px 5px 15px;
   font-size: 16px;
   outline: none;
   border: none;
   border-radius: 5px;
}

button {
   display: flex;
   align-items: center;
   justify-content: center;
   width: 140px;
   height: 40px;
   font-size: 16px;
   font-weight: 600;
   border: none;
   border-radius: 5px;
   cursor: pointer;
   background-color: #eff160;
}

button:hover,
button:focus {
   background-color: #7b60f1;
}

.gallery {
   margin-top: 90px;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   gap: 40px
}

.photo-card {
   background-color: rgba(185, 242, 250, 0.959);
   border: 1px solid #0d161b;
   border-radius: 5px;    
   padding: 20px;
}

.img_wrap {
   height: 280px;
   display: flex;
   align-items: center;
}

.gallery_link {
   display: block;
   text-decoration: none;
}

.load-more {
   margin: 40px auto;
   width: 200px;
   background-color: #eff160;
}